n;\r\n    position: relative;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n  }\r\n\r\n  &__menu-scroll {\r\n    width: calc(100% - 30px);\r\n    max-height: calc(100vh - 130px);\r\n    position: relative;\r\n    overflow: hidden;\r\n    overflow-y: auto;\r\n\r\n    @media(max-width: 767px) {\r\n      max-height: calc(100vh - 40px);\r\n    }\r\n\r\n    &::-webkit-scrollbar-track {\r\n      background-color:$dark;\r\n    }\r\n\r\n    &::-webkit-scrollbar-thumb {\r\n      -webkit-border-radius: 0px;\r\n      border-radius: 0px;\r\n      background-color:#5d6074;\r\n    }\r\n\r\n    &::-webkit-scrollbar-thumb:hover{\r\n      background-color:#5d6074;\r\n    }\r\n\r\n    &::-webkit-resizer{\r\n      width:3px;\r\n      height:0px\r\n    }\r\n\r\n    &::-webkit-scrollbar{\r\n      width: 3px;\r\n    }\r\n  }\r\n\r\n  &__menu-title {\r\n    font-weight: bold;\r\n    font-size: 26px;\r\n    color: #fff;\r\n    padding: 45px 40px 40px 40px;\r\n\r\n    @media(max-width: 767px) {\r\n      font-size: 20px;\r\n      padding: 20px;\r\n    }\r\n\r\n    &--mobile {\r\n      display: none;\r\n      @media(max-width: 767px) {\r\n        display: block;\r\n      }\r\n    }\r\n\r\n  }\r\n\r\n  &__menu-list {\r\n    &--mobile {\r\n      display: none;\r\n      @media(max-width: 767px) {\r\n        display: block;\r\n      }\r\n    }\r\n  }\r\n\r\n  &__menu-item {\r\n    position: relative;\r\n    margin: 0 10px 10px 10px;\r\n\r\n    &:last-child {\r\n      margin-bottom: 40px;\r\n    }\r\n  }\r\n\r\n  &__menu-link {\r\n    font-weight: 500;\r\n    font-size: 16px;\r\n    color: #fff;\r\n    text-decoration: none;\r\n    display: block;\r\n    background: rgba(255, 255, 255, 0.07);\r\n    border-radius: 7px;\r\n    padding: 13px 35px 12px 20px;\r\n    transition: background 0.3s;\r\n    position: relative;\r\n\r\n    @media(max-width: 767px) {\r\n      font-style: 14px;\r\n      padding: 10px 20px 10px 15px;\r\n    }\r\n\r\n    &:hover, &:focus {\r\n      background: rgba(255, 255, 255, 0.2);\r\n    }\r\n  }\r\n\r\n  &__menu-ico {\r\n    position: absolute;\r\n    right: 20px;\r\n    top: calc(50% - 7px);\r\n    fill: #FFF;\r\n    width: 14px;\r\n    height: 14px;\r\n    @media(max-width: 767px) {\r\n      width: 10px;\r\n      height: 10px;\r\n      top: calc(50% - 5px);\r\n      right: 15px;\r\n    }\r\n  }\r\n}\r\n","$primary: #191919;\n$dark: #41445C;\n$dark2: #53566c;\n$shadow-primary: rgba(65, 68, 92, 0.3);\n$shadow-dark2: rgba(248, 248, 248, 0.2);\n$grey: #818181;\n$light: #c4c4c4;"],"sourceRoot":""}]);
